nav {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #161616;
    padding: 20px 2%;
    position: sticky;
    top: 0;
    z-index: 1000;
}

nav ul {
    /* flex: 1;
    list-style: none;
    text-align: right;
    text-decoration: none;
    border-radius: 0.8rem; */
    display: flex;
}

nav ul li {
    display: inline-block;
    margin: 10px 20px;
    font-size: 18px; 
    list-style: none; 
    /* text-decoration: none;
    list-style: none; */
    /* background-color: white; */
    /* background-size: 10px */
}

nav ul li a {
    text-decoration: none;
    cursor: pointer;
    /* font-weight: bold; */
    color: white;
    /* background-color: #027f9b; */
    border-radius: .5rem;
    padding: 0.5rem;
    margin: 0 0.5rem;
    /* background-size: ; */
}

nav ul li a:not(.active):hover {
    background-color: rgb(34, 140, 66);
}

.logo {
    width: 50px;
    cursor: pointer;
    height: 50px;
    overflow: 'hidden';
    /* background-color: white; */
}

.toggle-icon {
    width: 50px;
    cursor: pointer;
    margin-left: 40px;
}

.search-box {
    display: flex;
    align-items: center;
    background: #242424;
    padding: 10px 20px;
    border-radius: 50px;
}

.active {
    background-color: rgb(13, 105, 86);
}


nav .menu {
    display: none;
    position: absolute;
    top: 0.75rem;
    right: 0.5rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
    cursor: pointer;
}

nav .menu span {
    height: 0.4rem;
    width: 100%;
    background-color: #fff;
    border-radius: 0.2rem;
    /* color: #fff; */
}

@media (max-width: 480px) {
    nav .menu {
        display: flex;
    }
    nav {
        flex-direction: column;
        align-items: start;
    }
    nav ul {
        display: none;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
    }
    nav ul.open {
        display: flex;
    }
    nav ul li {
        width: 100%;
        text-align: center;
    }
    nav ul li a {
        margin: 0.25rem 0.5rem;
    }
}
