
body {
    font-family: 'Arial, sans-serif';
  }
  
  .contact-container {
    padding: 20px;
    text-align: center;
  }
  
  .contact-title {
    font-size: 36px;
    margin-bottom: 20px;
    color: #c4a0c6;
    
  }
  
  .contact-section {
    margin-bottom: 40px;
  }
  
  .contact-section h2 {
    font-size: 28px;
    margin-bottom: 10px;
    color:#c4a0c6;
  }
  
  .contact-section p {
    font-size: 18px;
    margin: 5px 0;
    color:#c4a0c6;
  }
  
  .whatsapp-title {
    font-size: 28px;
    margin-bottom: 10px;
    color: #c4a0c6;
  }
  
  .location-title {
    font-size: 28px;
    margin-top: 20px;
    margin-bottom: 10px;
    color:#c4a0c6;
  }
  
  .map-frame {
    width: 100%;
    height: 800px;
    border: 0;
  }
  .working-hours {
    margin-bottom: 40px;
    margin-top: 20px;
    margin-bottom: 10px;
    color:#c4a0c6;
  }
  .whatsapp-icon{
    display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: #25d366;
  border: none;
  border-radius: 8px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  }
  
 